header {
    position: absolute;
    display: flex;
    justify-content: center;
    width: 100%;
    height: 15%;
}

.header_content{
    display: flex;
    justify-content: center;
}

.logo{
    display: flex;
    align-items: center;
    color: rgb(109, 250, 109);
    font-size: 2rem;
    font-family: "Helvetica Neue", Helvetica, Arial, sans-serif;
    margin: 50px;
    text-decoration: underline;
}

