nav{
    display: flex;
    justify-content: center;
    align-items: center;
    width: 200px;
    /* background-color: aqua; */
}

.navbar a {
    margin: 0 20px 0 20px;
    text-decoration: none;
    color: #b0aeae;
    font-size: 1.1rem;
    cursor: pointer;
}

.navbar a:hover {
    font-size: 1.3rem;
    position: relative;
    top: -10px;
    color: #fff;
}
