.post {
    position: relative;
    top: 80px;
    height: 100%;
    width: 100%;
    background-color: #3c3c3c;
    border-radius: 10px;
    padding: 50px;
}

.postTitle{
    width: fit-content;
    margin: 0 auto 40px auto;
    font-size: 1.8rem;
    color: #dddbdb;
    font-family: "Helvetica Neue", Arial, sans-serif;
}

.postContent{
    color: #b4b3b3;
}